import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			isVer : true,
			userName : '',
			userCard : '',
			userCard2 : '',
			isCardUpload : ''
		};
	},
	onLoad() {
		uni.showLoading({
			mask:true
		})
		this.initData();
	},
	methods:{
		initData(){
			let _this = this;
			http.get('bindBankCard/tobind',{phone:this.$store.state.userInfo.phone}).then((res)=>{
				if(res.data.data == undefined){
					_this.isVer = false
				}else{
					_this.isVer = true
					_this.userName = res.data.data.realName;
					_this.userCard = res.data.data.idCard;
				}
			})
		},
		uploadCardImg(){
			let _this = this;
			   uni.chooseImage({
				count: 1,
				sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
				success: function (res) {
					uni.showLoading({
						mask:true
					});
					const tempFilePaths = res.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url : _this.$store.state.webPath + 'file/upload',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {},
						success: function (uploadFileRes) {
							uni.hideLoading()
							_this.isCardUpload = JSON.parse(uploadFileRes.data).data;
						},
						error(){
							uni.hideLoading()
						}
					});
				},
				error : function(e){
					console.log(e);
				}
			});
		},
		goverified(){
			if(this.userName == '' || this.userCard == '' || this.userCard2 == ''){
				uni.showModal({
					title : '提示',
					content : '输入信息不能为空',
					showCancel : false,
				})
				return;
			}
			if(this.userCard != this.userCard2){
				uni.showModal({
					title : '提示',
					content : '两次输入身份证信息不一致',
					showCancel : false,
				})
				return
			}
			if(this.isCardUpload == ''){
				uni.showModal({
					title : '提示',
					content : '请先上传身份证照片',
					showCancel : false,
				})
				return
			}
			uni.showLoading({
				mask:true
			})
			http.get('member/realName',{
				realName:this.userName,
				idCard:this.userCard,
				phone:this.$store.state.userInfo.phone,
				idCardImg:this.isCardUpload,
			}).then((res)=>{
				uni.showModal({
					title : '提示',
					content : '实名认证已提交',
					showCancel : false,
				})
				this.initData();
			})
		}
	}
}
